import React, { useState, useEffect } from "react";
import { useForm, FormProvider } from "react-hook-form";
import moment from "moment";
import { yupResolver } from "@hookform/resolvers/yup";
import yupFormSchemas from "src/modules/shared/yup/yupFormSchemas";
import { i18n } from "src/i18n";
import * as yup from "yup";
import FilesFormItem from "src/view/shared/form/items/FilesFormItem";
import Storage from "src/security/storage";
import TextAreaFormItem from "src/view//shared/form/items/TextAreaFormItem";
import ButtonIcon from "src/view/shared/ButtonIcon";
import publicationEnumerators from "src/modules/publication/publicationEnumerators";
import RadioFormItem from "src/view/shared/form/items/RadioFormItem";
import CategoryPublicationAutocompleteFormItem from "../autocomplete/PublicationAutocompleteFormItem";
import AproposService from "src/modules/Apropos/AproposService";
import ImagesFormItem from "src/view/shared/form/items/ImagesFormItem";
import ImagesUploadFormItem from "src/view/shared/form/items/ImagesUploadFormItem";
import InputFormItem from "src/view/shared/form/items/InputFormItem";

const schema = yup.object().shape({
  thematique: yupFormSchemas.relationToOne(
    i18n("entities.publication.fields.thematique"),
    {}
  ),
  category: yupFormSchemas.relationToOne(
    i18n("entities.publication.fields.categorie"),
    {
      required: false,
    }
  ),
  supportsAR: yupFormSchemas.files(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  photosAR: yupFormSchemas.images(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  supports: yupFormSchemas.files(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  photos: yupFormSchemas.images(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  supportsEN: yupFormSchemas.files(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  photosEN: yupFormSchemas.images(i18n("entities.publication.fields.photos"), {
    required: false,
  }),
  titleAR: yupFormSchemas.string(i18n("entities.publication.fields.titleAR"), {
    required: false,
  }),
  titleFR: yupFormSchemas.string(i18n("entities.publication.fields.titleFR"), {
    required: false,
  }),
  titleEN: yupFormSchemas.string(i18n("titleEN"), {
    required: false,
  }),

  descriptionFR: yupFormSchemas.string(
    i18n("entities.publication.fields.description"),
    {
      required: false,
    }
  ),
  descriptionAR: yupFormSchemas.string(
    i18n("entities.publication.fields.description"),
    {
      required: false,
    }
  ),
  descriptionEN: yupFormSchemas.string(
    i18n("entities.publication.fields.description"),
    {
      required: false,
    }
  ),
});
function PublicationForm(props) {
  const [initialValues] = useState(() => {
    const record = props.record || {};

    return {
      titleAR: record.titleAR,
      titleFR: record.titleFR,
      titleEN: record.titleEN,
      thematique: record.thematique,
      category: record.category,
      type: record.type,
      supportsAR: record.supportsAR || [],
      photosAR: record.photosAR || [],
      supports: record.supports || [],
      photos: record.photos || [],
      supportsEN: record.supportsEN || [],
      photosEN: record.photosEN || [],
      descriptionFR: record.descriptionFR,
      descriptionAR: record.descriptionAR,
      descriptionEN: record.descriptionEN,
      statut: record.statut,
    };
  });
  const currentlySelectedLanguage = localStorage.getItem("language");

  const form = useForm({
    resolver: yupResolver(schema),
    mode: "all",
    defaultValues: initialValues as any,
  });

  const onReset = () => {
    Object.keys(initialValues).forEach((key) => {
      form.setValue(key, initialValues[key]);
    });
  };

  const onSubmit = (values) => {
    props.onSubmit(props?.record?.id, values);
  };
  const { saveLoading } = props;
  return (
    <FormProvider {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)}>
        <div className="container__form">
          {/* CATEGORIES */}
          <CategoryPublicationAutocompleteFormItem
            name="category"
            label={i18n("entities.publication.fields.categorie")}
            required={false}
            showCreate={!props.modal}
          />

          <RadioFormItem
            name="type"
            label={i18n("entities.publication.fields.type")}
            options={publicationEnumerators.type.map((value) => ({
              value,
              label: value,
            }))}
            className="contentTypeRadio"
          />

          {currentlySelectedLanguage === "fr" ? (
            <>
              <InputFormItem
                name="titleFR"
                label={i18n("entities.publication.fields.title")}
                required={false}
              />
              <ImagesUploadFormItem
                name="photos"
                label={i18n("entities.publication.fields.photos")}
                required={false}
                storage={Storage.values.publicationPhotos}
                max={1}
              />
              <FilesFormItem
                name="supports"
                label={i18n("entities.publication.fields.supports")}
                required={false}
                storage={Storage.values.publicationSupports}
                max={1}
                formats={undefined}
              />
              <TextAreaFormItem
                name="descriptionFR"
                label={i18n("entities.publication.fields.description")}
                required={false}
              />
            </>
          ) : currentlySelectedLanguage === "en" ? (
            <>
              <InputFormItem
                name="titleEN"
                label={i18n("entities.publication.fields.title")}
                required={false}
              />
              <ImagesUploadFormItem
                name="photosEN"
                label={i18n("entities.publication.fields.photos")}
                required={false}
                storage={Storage.values.publicationPhotos}
                max={1}
              />
              <FilesFormItem
                name="supportsEN"
                label={i18n("entities.publication.fields.supports")}
                required={false}
                storage={Storage.values.publicationSupports}
                max={1}
                formats={undefined}
              />
              <TextAreaFormItem
                name="descriptionEN"
                label={i18n("entities.publication.fields.description")}
                required={false}
              />
            </>
          ) : (
            <>
              <InputFormItem
                name="titleAR"
                label={i18n("entities.publication.fields.title")}
                required={false}
              />
              <ImagesUploadFormItem
                name="photosAR"
                label={i18n("entities.publication.fields.photos")}
                required={false}
                storage={Storage.values.publicationPhotos}
                max={1}
              />
              <FilesFormItem
                name="supportsAR"
                label={i18n("entities.publication.fields.supports")}
                required={false}
                storage={Storage.values.publicationSupports}
                max={1}
                formats={undefined}
              />
              <TextAreaFormItem
                name="descriptionAR"
                label={i18n("entities.publication.fields.description")}
                required={false}
              />
            </>
          )}

          <div className="item__button">
            <button
              className="cancel__button"
              onClick={onReset}
              type="button"
              disabled={saveLoading}
            >
              {i18n("common.reset")}
            </button>
            <button
              className="form__button"
              onClick={form.handleSubmit(onSubmit)}
              disabled={saveLoading}
            >
              <ButtonIcon loading={saveLoading} />
              {i18n("common.save")}
            </button>
          </div>
        </div>
      </form>
    </FormProvider>
  );
}

export default PublicationForm;
